.FacetNav {
  padding-top: 5px;
  padding-bottom: 5px;
  border: 1px solid $gray-dark;

  &__row {
    background-color: #fff;

    div[class*=' col-'] {
      height: 200px;
    }
  }

  &__showMoreContainer {
    display: flex;
    justify-content: flex-end;
    padding-right: 15px;
    &__showMore {
      margin-right: 15px;
      margin-top: 15px;
    }
    
    .FacetNav__showMore:focus {  // somehow &__showMore:focus doesn't work
      outline-color: $primary-action-color;
    }
  }
}